<template>
	<view class="question_contain">
		<view>
			<view class="example-body">
				<uni-nav-bar :fixed="true" color="#333333" background-color="#FFFFFF" @clickRight="handleSearchOpen">
					<view slot="left" @click="handle_back"><text class="iconfont iconfanhui f18"></text></view>
					<view slot="left"><text class="iconfont iconfengexian f18 "></text></view>
					<view slot="left" @click="handle_plat"><text class="iconfont iconguanbi f18 pl8"></text></view>
					<view class="input-view f18 uni_3030">平城热线</view>
					<!-- <block slot="right"><text class="iconfont iconsousuo f22"></text></block> -->
				</uni-nav-bar>
			</view>
		</view>
		<view class="uni-column uni_title mb10">
			<view class="uni-label f16">诉求标题</view>
			<input class="uni-input" v-model="param.networkTitle" type="input" placeholder="请填写诉求标题" />
		</view>
		<view class="question_view mb10">
			<view class="uni-column uni-divider-line">
				<view class="uni-label f16">诉求类别</view>
				<picker class="uni_picker" mode="selector" :range="pickerDate" :range-key="'label'" :value="index" @change="handlerPicker">
					<view :class="!param.hotlineAppealCategory ? 'pickerCategory' : 'picker_active'">
						{{ !!param.hotlineAppealCategory ? param.hotlineAppealCategory : '请选择诉求类别' }}
					</view>
				</picker>
			</view>
			<view class="question_radio uni-divider-line">
				<view class="uni-label mb15 f16 mt14">诉求类型</view>
				<view class="uni-flex-list_type ">
					<template v-for="item in hotlineAppealTypeDate">
						<button
							type="default"
							:class="hotlineAppealTypeId != item.value ? 'uni_question_btntype' : 'quset_active'"
							class="uni_question_btntype f16 mb15"
							@click="handleBtnType(item)"
						>
							{{ hotlineAppealType == item.label ? hotlineAppealType : item.label }}
						</button>
					</template>
				</view>
			</view>
			<view class="appealContent  mt14">
				<view class="uni-label mb14 f16">诉求内容</view>
				<textarea
					class="textarea"
					disable-default-padding="true"
					:focus="focus_content"
					v-model="param.networkContent"
					v-if="placeholder_show"
					value=""
					placeholder=""
					@input="handler_textarea"
					@focus="handler_textarea"
					@blur="handler_content"
					maxlength="200"
				/>
				<view v-else class="placeholder_color" @click="handler_content">
					<view>1.请您在留言前详细阅读“用户须知”</view>
					<view>2.在留言时请详细描述您的诉求发生的时间地点及必要信息，以使相关部门问题更好的处理。</view>
				</view>
				<view class="appealContent_num f14 ">{{ appealContent_num }}</view>
			</view>
		</view>
		<view class="imgBackgrouind ">
			<view class="uni-label mb15 f16">上传照片</view>
			<view class="uni-uploader-body">
				<view class="uni-uploader__files">
					<view v-for="(image, index) in imageList" :key="index">
						<view class="uni-uploader__file" style="position: relative;">
							<image class="uni-uploader__img" :src="img_url + image" :data-src="img_url +image" mode="aspectFill" @tap="previewImage"></image>
							<text class="iconfont iconshanchu f22 iconfont_dele" @click="handle_delete(image, index)"></text>
						</view>
					</view>
					<view v-if="imageList.length < 3" class="uni-uploader__input-box"><view class="uni-uploader__input" @tap="chooseImage"></view></view>
				</view>
			</view>
		</view>
		<view class="uni_p15">
			<label><checkbox value="cb" :checked="checked" @click="handlee_check" /></label>
			<text class=" userNotice">我已仔细阅读</text>
			<text class="uni_c114 f12 " @click="handler_notice_user">用户须知</text>
		</view>
		<view class="mt30 pb50">
			<button class="question_btn" v-if="!checked" @click="handleSubmit_checked">提交</button>
			<button class="question_btn question_btn_active" v-else @click="handleSubmit">提交</button>
		</view>
	</view>
</template>

<script>
export default {
	components: {},
	data() {
		return {
			img_url: this.$config.imgPath,
			param: {
				isOpen: '2',
				networkContent: '',
				networkId: '',
				networkState: '',
				networkTitle: '',
				networkType: '',
				submitterId: '',
				submitterName: '',
				submitterPhone: '',
				deviceId: '',
				jurisdiction: '',
				networkFile: '',
				sessionId: '',
				hotlineAppealTitle: '',
				hotlineAppealCategoryId: '',
				hotlineAppealCategory: ''
			},
			hotlineAppealType: '咨询',
			hotlineAppealTypeId: '1',
			index: 0,
			hotlineAppealCategory: '',
			pickerDate: this.$configType.network_state_enum,
			current: 0,
			hotlineAppealTypeDate: this.$configType.network_type_enum,
			appealContent_num: '0/200',
			imageList: [],
			placeholder_show: false,
			checkRes: false,
			checked: false,
			focus_content: false
		};
	},
	onTabItemTap(object) {
		if (object.index == 1) {
			uni.hideTabBar();
		}
	},
	onLoad() {
		uni.hideTabBar();
		this.getNetworkId();
	},
	onShow() {
		uni.hideTabBar();
		this.param.submitterId = uni.getStorageSync('submitterId');
		this.param.submitterName = uni.getStorageSync('submitterName');
		this.param.submitterPhone = uni.getStorageSync('submitterPhone');
		this.getNetworkId();
	},
	mounted() {
		window.chooseImage_app = this.chooseImage_app;
	},
	methods: {

		/*S 返回*/
		handle_back() {
			var _this=this;
			if (!!_this.param.networkTitle || !!_this.param.hotlineAppealCategory || !!_this.param.networkContent) {
				uni.showModal({
					title: '温馨提示',
					content: '退出当前页数据将不保存，是否继续退出?',
					success: function(res) {
						if (res.confirm) {
							_this.handler_clear();
							uni.switchTab({
								url: '/pages/index/index'
							});
						} else if (res.cancel) {
							return;
						}
					}
				});
			}else{
				_this.handler_clear();
				uni.switchTab({
					url: '/pages/index/index'
				});
			}
			
		},
		/*E 返回*/
		/*S 导航 搜索按钮 */
		handleSearchOpen() {
			uni.navigateTo({
				url: '/pages/searchAttention/index'
			});
		},
		/*E导航 搜索按钮 */
		/*S 关闭按钮*/
		handle_close() {
			var _this=this;
			
			if (!!_this.param.networkTitle || !!_this.param.hotlineAppealCategory || !!_this.param.networkContent) {
				uni.showModal({
					title: '温馨提示',
					content: '退出当前页数据将不保存，是否继续退出?',
					success: function(res) {
						if (res.confirm) {
							_this.handler_clear();
							if (uni.getSystemInfoSync().platform == 'ios') {
								window.location = 'ios://details';
							} else {
								window.android.finish();
							}
						} else if (res.cancel) {
							return;
						}
					}
				});
			}else{
				if (uni.getSystemInfoSync().platform == 'ios') {
					window.location = 'ios://details';
				} else {
					window.android.finish();
				}
			}
		},
		handle_plat() {
			var _this = this;
			_this.handle_close();
		},
		/*e 关闭按钮*/
		/*S 清空数据*/
		handler_clear() {
			this.param.isOpen = '2';
			this.param.networkContent = '';
			this.param.networkState = '';
			this.param.hotlineAppealCategory = '';
			this.param.hotlineAppealCategoryId = '';
			this.param.networkTitle = '';
			this.hotlineAppealType = '咨询';
			this.hotlineAppealTypeId = '1';
			this.imageList = [];
			this.placeholder_show = false;
			this.appealContent_num = '0/200';
			this.checked = false;
			uni.setStorageSync('formdata', {});
		},
		/*S 诉求内容*/
		handler_content() {
			this.focus_content = true;
			if (this.param.networkContent) {
				this.placeholder_show = true;
			} else {
				this.placeholder_show = !this.placeholder_show;
			}
			if (this.placeholder_show) {
				this.focus_content = true;
			} else {
				this.focus_content = false;
			}
		},
		handler_textarea(e) {
			this.appealContent_num = e.target.value.length + '/200';
			if (e.target.value.length >= 200) {
				return;
			} else {
				this.param.networkContent = e.target.value;
			}
		},
		/*E 诉求内容*/
		/*S 生成工单编号*/
		getNetworkId() {
			this.$minApi.getNetworkId({ deviceId: '', sessionId: '' }).then(res => {
				this.param.networkId = res.data;
			});
		},
		/*E 生成工单编号*/
		/*S 诉求类别 列表 */
		handlerPicker(data) {
			this.param.hotlineAppealCategory = this.pickerDate[data.detail.value].label;
			this.param.hotlineAppealCategoryId = this.pickerDate[data.detail.value].value;
			this.param.networkState = this.pickerDate[data.detail.value].value;
		},
		/*E 诉求类别 列表*/
		/*S 诉求类型*/
		handleBtnType(item) {
			this.hotlineAppealType = item.label;
			this.hotlineAppealTypeId = item.value;
		},
		/*E 诉求类型*/
		/*S 上传照片 */
		chooseImage() {
			if (uni.getSystemInfoSync().platform == 'ios') {
				this.chooseImage_ios();
			} else {
				var index = 3 - this.imageList.length;
				window.android.showPhonoDialog(index);
			}
		},
		chooseImage_app(str) {
			if (this.imageList.length >= 3) {
				uni.showToast({
					title: '最多上传3张图片'
				});
				return;
			}
			if (!!str) {
				this.imageList.push(str);
			} else {
				this.imageList = [];
			}
		},
		chooseImage_ios() {
			if (this.imageList.length >= 3) {
				uni.showToast({
					title: '最多上传3张图片'
				});
				return;
			}
			var _this = this;
			uni.chooseImage({
				sourceType: ['album'], //从相册选择,
				count: 5,
				success: res => {
					const tempFilePaths = res.tempFilePaths;
					const uploadTask = uni.uploadFile({
						url: '/picture-web/outer/photo/uploadFile',
						filePath: res.tempFilePaths[0],
						formData: {
							user: res.tempFiles[0],
							file: res.tempFiles[0]
						},
						name: 'file',
						success: function(uploadFileRes) {
							let path = JSON.parse(uploadFileRes.data);
							_this.imageList.push(path.data.filePath);
						},
						fail: function(err) {
							uni.uploadFile({
								url: '/picture-web/outer/photo/uploadFile',
								filePath: tempFilePaths[0],
								formData: {
									user: tempFilePaths[0],
									file: res.tempFiles[0]
								},
								fileType: 'image',
								name: 'file',
								success: function(uploadFileRes) {
									let path = JSON.parse(uploadFileRes.data);
									_this.imageList.push(path.data.filePath);
								}
							});
						}
					});
				},
				fail: err => {}
			});
		},
		/*S 删除*/
		handle_delete(item, index) {
			this.imageList.splice(index, 1);
		},
		
		/*E 删除*/
		/*S 预览*/
		previewImage: function(e) {
			var current = e.target.dataset.src
			var array = [];
			this.imageList.map(i=>{
				array.push('/'+i)
			})
			uni.previewImage({
				current: current,
				urls: array
			})
		},
		/*E 预览*/
		/*E 上传照片*/
		handlee_check(e) {
			this.checked = !this.checked;
		},
		/*S 提交表单*/
		handleSubmit() {
			this.param.networkState = this.param.hotlineAppealCategoryId;
			this.param.networkType = this.hotlineAppealTypeId;
			this.param.networkFile = this.imageList;
			if (!this.param.networkTitle) {
				uni.showToast({ title: '请输入诉求标题', icon: 'none' });
				return;
			} else if (!this.param.networkState) {
				uni.showToast({ title: '请选择诉求类别', icon: 'none' });
				return;
			} else if (!this.param.networkType) {
				uni.showToast({ title: '请选择诉求类型', icon: 'none' });
				return;
			} else if (!this.param.networkContent) {
				uni.showToast({ title: '请输入诉求内容', icon: 'none' });
				return;
			} else if (!this.checked) {
				uni.showToast({ title: '请选择我已仔细阅读', icon: 'none' });
				return;
			} else {
				this.checkRes = true;
				this.handleSubmit_btn();
			}
		},
		handleSubmit_btn() {
			if (this.checkRes) {
				/* this.param.submitterId = '210';
				this.param.submitterName = '雷霆哈哈';
				this.param.submitterPhone = '210'; */
				this.param.networkFile = JSON.stringify(this.param.networkFile);
				this.$minApi
					.networkWorkOrderSave({ ...this.param })
					.then(res => {
						if (res.code == '999999999') {
							uni.showToast({ title: res.msg, icon: 'none' });
						} else if (res.code == '0') {
							uni.showToast({ title: '提交成功' });
							var _this=this;
							setTimeout(function(){
								_this.handler_clear();
								uni.switchTab({
									url: '/pages/index/index'
								})
							},1000)
						} else {
							uni.showToast({ title: res.msg, icon: 'none' });
						}
					})
					.then(rej => {
						
					});
			}
		},
		handleSubmit_checked() {
			uni.showToast({ title: '请勾选我已仔细阅读', icon: 'none' });
		},
		/*E 提交表单*/
		/*S 用户须知*/
		handler_notice_user() {
			uni.navigateTo({
				url: '/pages/question/noticeUser'
			});
		}
		/*E 用户须知*/
	}
};
</script>

<style lang="scss">
.question_contain {
	background-color: #f7f7f7;
	.question_btn {
		width: 494upx;
		height: 88upx;
		background: #c8cdd5;
		border-radius: 44upx;
		color: #ffffff;
	}
	.question_radio {
		background-color: #ffffff;
	}
	.question_label {
		display: block;
		width: 190upx;
		height: 64upx;
		background: #f1f1f1;
		border-radius: 36upx;
		radio {
			display: none;
		}
		.question_radio_btn {
			display: inline-block;
			width: 50%;
		}
		.question_radio_btnTip {
			width: 50%;
			display: inline-block;
		}
	}
	.uni_question_btntype {
		height: 64upx;
		line-height: 36upx;
		padding: 16upx 62upx;
		border-radius: 36upx;
		color: #6c6c6c;
		background-color: #f1f1f1;
	}
	.quset_active {
		color: #4a93fe;
		background-color: #e2ecfe;
	}
	uni-button:after {
		border: none;
	}
}
.question_view {
	padding: 0upx 30upx;
	background-color: #ffffff;
}
.uni-form-item {
	display: flex;
}
.uni-label {
	width: 160upx;
}
.uni_picker {
	width: 70%;
	padding: 7px 12px;
}
.pickerCategory {
	color: #939393;
}
.picker_active {
	color: #333333;
}
.uni-column {
	height: 92upx;
	display: flex;
	flex-direction: row;
	align-items: center;
	background-color: #ffffff;
}
.uni_title {
	padding: 0upx 30upx;
}
.uni-input {
	width: 80%;
	height: 100% !important;
}
.appealContent {
	background-color: #ffffff;
}
uni-textarea {
	width: 100%;
}
.appealContent_num {
	color: #939393;
	text-align: right;
	padding-bottom: 30upx;
}
.imgBackgrouind {
	padding: 30upx;
	background-color: #ffffff;
}
.photosUpload {
	width: 220upx;
	height: 220upx;
	background-color: #f2f2f2;
	text-align: center;
	line-height: 280upx;
	image {
		width: 78upx;
		height: 68upx;
	}
}
.uni-flex-list_type {
	display: flex;
	flex-wrap: wrap;
	justify-content: flex-start;
}
.placeholder_color {
	color: #939393;
	font-size: 28upx;
	padding: 0upx 30upx;
	height: 316upx;
	margin-top: 26upx;
}
.textarea {
	padding-left: 20upx;
	margin-top: 26upx;
	height: 316upx;
}

.userNotice {
	color: #585858;
	font-size: 24upx;
	margin-left: 14upx;
	margin-right: 10upx;
}
.question_btn_active {
	background-color: #3893fe !important;
}
.iconfont_dele {
	position: absolute;
	right: 8upx;
	top: 2upx;
}
</style>
